import React from 'react'
import Elements from './elements/Elements'
import { } from 'react-router'

import {
    BrowserRouter as Router,
    Switch,
    Route,
    Link
} from "react-router-dom";

interface INav {
    currentNavLocation: string
}

const Nav = (props: INav) => {
    return (
        <Elements.nav>
            <Elements.navList>
                
                <Elements.navItem
                    isActive={
                        props.currentNavLocation === "/profile" ||
                        props.currentNavLocation === '/' ||
                        props.currentNavLocation === ''
                    }
                >
                    <Elements.navLink 
                        isActive={
                            props.currentNavLocation === "/profile" ||
                            props.currentNavLocation === '/' ||
                            props.currentNavLocation === ''
                        }
                        href="#/profile"
                    >
                        Perfil
                    </Elements.navLink>
                </Elements.navItem>
                
                <Elements.navItem isActive={props.currentNavLocation === "/skills"}>
                    <Elements.navLink isActive={props.currentNavLocation === "/skills"} href="#/skills">Conocimientos y Habilidades</Elements.navLink>
                </Elements.navItem>
                
                <Elements.navItem isActive={props.currentNavLocation === "/experience"}>
                    <Elements.navLink isActive={props.currentNavLocation === "/experience"} href="#/experience">Experiencia</Elements.navLink>
                </Elements.navItem>
                
                <Elements.navItem isActive={props.currentNavLocation === "/data"}>
                    <Elements.navLink isActive={props.currentNavLocation === "/data"} href="#/data">Datos</Elements.navLink>
                </Elements.navItem>
                
                <Elements.navItem isActive={props.currentNavLocation === "/education"}>
                    <Elements.navLink isActive={props.currentNavLocation === "/education"} href="#/education">Educación</Elements.navLink>
                </Elements.navItem>
            
            </Elements.navList>
        </Elements.nav>
    )
}

export default Nav 