import * as React from "react";
import { render } from "react-dom";
import { Provider } from "react-redux";
import { HashRouter as Router } from "react-router-dom";
import { ThemeProvider } from 'styled-components'
import appGenerator from './AppGenerator'

const resetStyles = () => {
    const bodyEl = document.querySelector('body')
    const rootEl = document.getElementById("root")
    if (rootEl && bodyEl) {
      rootEl.style.padding = '0px'
      rootEl.style.margin = '0px'
      bodyEl.style.padding = '0px'
      bodyEl.style.margin = '0px'
      bodyEl.style.background = '#E0E5EC'
    }    
}

const indexGenerator = (Routes: any, store: any, theme: any) => {
    resetStyles()

    const renderApp = (Component: any) => {
      render(
        <Provider store={store as any}>
          <ThemeProvider theme={theme}>
            <Router>
              <Component />
            </Router>
          </ThemeProvider>
        </Provider>,
        document.getElementById("root")
      )
    }

    renderApp(appGenerator(Routes))
}

export default indexGenerator
